// src/pages/authentication/UpdatePassword.jsx

import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Card,
  CardContent,
  Button,
  TextField,
  Typography,
  Snackbar,
  Grid,
  Stack,
} from "../../components/index.js"; // Ajuste o caminho conforme sua estrutura
import { supabase } from "../../services/SupabaseClient"; // Ajuste o caminho conforme sua estrutura

const UpdatePassword = () => {
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [showSnackbar, setShowSnackbar] = useState(false);
  const [snackbarMsg, setSnackbarMsg] = useState("");
  const navigate = useNavigate();

  const handleUpdatePassword = async (e) => {
    e.preventDefault();
    setLoading(true);

    if (password !== confirmPassword) {
      setSnackbarMsg("As senhas no coincidem!");
      setShowSnackbar(true);
      setLoading(false);
      return;
    }

    try {
      // Chama a API do Supabase para atualizar a senha do usurio logado pela URL
      const { error } = await supabase.auth.updateUser({ password });

      if (error) {
        throw error;
      }

      setSnackbarMsg("Senha atualizada com sucesso! Redirecionando...");
      setShowSnackbar(true);

      setTimeout(() => {
        navigate("/login"); // Volta para o login aps o sucesso
      }, 3000);
    } catch (error) {
      console.error("Erro ao atualizar a senha:", error);
      setSnackbarMsg(error.message);
      setShowSnackbar(true);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Grid
      container
      justifyContent="center"
      alignItems="center"
      style={{
        minHeight: "100vh",
        background: "linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)",
        padding: "16px",
      }}
    >
      <Grid item xs={12} sm={8} md={4}>
        <Card
          style={{
            padding: 24,
            borderRadius: 16,
            boxShadow: "0 4px 24px rgba(0,0,0,0.08)",
          }}
        >
          <CardContent>
            <form style={{ width: "100%" }} onSubmit={handleUpdatePassword}>
              <Stack direction="column" alignItems="center" spacing={2}>
                <Typography
                  variant="h5"
                  style={{ fontWeight: 700, marginBottom: 8 }}
                >
                  Nova Senha
                </Typography>
                <TextField
                  label="Nova Senha"
                  type="password"
                  fullWidth
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
                <TextField
                  label="Confirmar Nova Senha"
                  type="password"
                  fullWidth
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  required
                />
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  style={{ width: "100%" }}
                  disabled={loading}
                >
                  {loading ? "Salvando..." : "Salvar Nova Senha"}
                </Button>
              </Stack>
            </form>
          </CardContent>
        </Card>
        <Snackbar
          open={showSnackbar}
          autoHideDuration={4000}
          onClose={() => setShowSnackbar(false)}
          message={snackbarMsg}
        />
      </Grid>
    </Grid>
  );
};

export default UpdatePassword;
