import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import {
  Grid,
  Card,
  Typography,
  Stack,
  Button,
  Rating,
  Snackbar,
  Fab,
  TextField,
} from "../components";
import RecipeAPI from "../services/RecipeAPI";
import Database from "../services/Database";

const RecipeDetails = () => {
  const { recipeId } = useParams();

  // ESTADOS
  const [recipe, setRecipe] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [userRating, setUserRating] = useState(0);
  const [userComment, setUserComment] = useState("");
  const [isFavorited, setIsFavorited] = useState(false);
  const [showSnackbar, setShowSnackbar] = useState(false);
  const [snackbarMsg, setSnackbarMsg] = useState("");

  // MOCK: UUID Mock que o banco aceita
  const MOCK_USER_ID = "00000000-0000-0000-0000-000000000001";

  // Funo para salvar (ou remover) a receita como favorita
  const handleToggleFavorite = async () => {
    try {
      // Lgica de CREATE/DELETE
      const isFav = await Database.toggleFavorite(
        MOCK_USER_ID,
        Number(recipeId),
        recipe.title,
        isFavorited
      );

      setIsFavorited(isFav);
      setSnackbarMsg(
        isFav
          ? "Receita salva com sucesso!"
          : "Receita removida dos seus favoritos."
      );
    } catch (err) {
      setSnackbarMsg("Erro ao salvar favorito.");
    } finally {
      setShowSnackbar(true);
    }
  };

  // Funo para lidar com a classificao por estrelas e comentrio
  const handleRatingChange = async (newValue) => {
    // Se a nota for nula ou 0, usa a nota atual (para cliques no boto Salvar)
    const ratingValue =
      newValue === null || newValue === 0 ? userRating : newValue;

    setUserRating(ratingValue);

    // S salva se houver nota
    if (ratingValue > 0) {
      try {
        // Lgica de CREATE/UPDATE com o comentrio
        await Database.saveRating(
          MOCK_USER_ID,
          Number(recipeId),
          ratingValue,
          userComment
        );

        setSnackbarMsg(
          `Sua classificao (${ratingValue} estrelas) foi salva!`
        );
      } catch (err) {
        setSnackbarMsg("Erro ao salvar a classificao.");
      } finally {
        setShowSnackbar(true);
      }
    }
  };

  // Funo para salvar o comentrio (chamada APENAS pelo boto Salvar Comentrio)
  const handleSaveComment = async () => {
    if (userRating === 0) {
      setSnackbarMsg("Por favor, d uma nota antes de salvar o comentrio.");
      setShowSnackbar(true);
      return;
    }
    // Reutiliza o handleRatingChange com a nota existente
    await handleRatingChange(userRating);
  };

  // Lgica para buscar os detalhes completos da receita
  const fetchRecipeDetails = async () => {
    if (!recipeId) return;

    setIsLoading(true);
    setError(null);

    try {
      const data = await RecipeAPI.getRecipeDetails(recipeId);
      setRecipe(data);

      // Verificao de status de Favorito
      const isFav = await Database.isFavorited(MOCK_USER_ID, Number(recipeId));
      setIsFavorited(isFav);
    } catch (err) {
      setError("No foi possvel carregar os detalhes desta receita.");
    } finally {
      setIsLoading(false);
    }
  };

  // Efeito para carregar os dados ao montar o componente
  useEffect(() => {
    fetchRecipeDetails();
  }, [recipeId]);

  // Funo auxiliar para limpar o HTML (modo de preparo da Spoonacular)
  const cleanHtml = (html) => {
    if (!html) return "";
    const tempDiv = document.createElement("div");
    tempDiv.innerHTML = html;
    return tempDiv.textContent || tempDiv.innerText || "";
  };

  // --- Renderizao de Estados de Carregamento/Erro ---
  if (isLoading) {
    return (
      <Grid
        container
        justifyContent="center"
        alignItems="center"
        style={{
          minHeight: "100vh",
          background: "linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)",
        }}
      >
        <Typography variant="h6">Carregando detalhes da receita...</Typography>
      </Grid>
    );
  }

  if (error || !recipe) {
    return (
      <Grid
        container
        justifyContent="center"
        alignItems="center"
        style={{
          minHeight: "100vh",
          background: "linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)",
        }}
      >
        <Typography variant="h6" color="error">
          Erro: {error || "Receita no encontrada."}
        </Typography>
      </Grid>
    );
  }

  const instructions = cleanHtml(recipe.instructions || recipe.summary);

  // --- Renderizao Principal (Mobile-First Moderno) ---
  return (
    // Fragmento para envolver o Grid e o Snackbar
    <>
      <Grid
        container
        justifyContent="center"
        style={{
          background: "linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)",
          padding: "16px 8px",
        }}
      >
        <Grid item xs={12} sm={10} md={8}>
          <Stack
            spacing={3}
            style={{ paddingTop: "16px", paddingBottom: "32px" }}
          >
            {/* 1. TTULO E FAVORITAR */}
            <Stack
              direction="row"
              justifyContent="space-between"
              alignItems="center"
            >
              <Typography
                variant="h4"
                style={{
                  fontWeight: 800,
                  color: "#1976d2",
                  textAlign: "left",
                  flexGrow: 1,
                }}
              >
                {recipe.title}
              </Typography>

              {/* BOTO DE FAVORITAR */}
              <Fab
                color={isFavorited ? "secondary" : "primary"}
                aria-label="favorite"
                onClick={handleToggleFavorite}
                style={{
                  marginLeft: "16px",
                  minWidth: "56px",
                  background: isFavorited ? "#F44336" : "#1976d2",
                }}
              >
                <Typography style={{ fontSize: "20px", color: "#fff" }}>
                  {isFavorited ? "" : ""}
                </Typography>
              </Fab>
            </Stack>

            {/* 2. IMAGEM E TEMPO DE PREPARO (SEO DE METADADOS) */}
            <Card
              style={{
                padding: 0,
                borderRadius: 16,
                boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
              }}
            >
              {/* Imagem principal */}
              <div
                style={{
                  height: "250px",
                  backgroundImage: `url(${recipe.image})`,
                  backgroundSize: "cover",
                  backgroundPosition: "center",
                  borderTopLeftRadius: "16px",
                  borderTopRightRadius: "16px",
                }}
              />

              {/* Tempo de Preparo e Pores */}
              <Stack
                direction="row"
                justifyContent="space-around"
                style={{ padding: "12px", background: "#e3f2fd" }}
              >
                <Stack alignItems="center">
                  <Typography
                    variant="subtitle1"
                    style={{ fontWeight: 700, color: "#1976d2" }}
                  >
                    {recipe.readyInMinutes} min
                  </Typography>
                  <Typography variant="caption" color="textSecondary">
                    Preparo
                  </Typography>
                </Stack>
                <Stack alignItems="center">
                  <Typography
                    variant="subtitle1"
                    style={{ fontWeight: 700, color: "#1976d2" }}
                  >
                    {recipe.servings}
                  </Typography>
                  <Typography variant="caption" color="textSecondary">
                    Pores
                  </Typography>
                </Stack>
              </Stack>
            </Card>

            {/* 3. CLASSIFICAO E COMENTRIO */}
            <Card
              style={{
                padding: 16,
                borderRadius: 16,
                boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
              }}
            >
              <Stack spacing={2}>
                {/* RATING */}
                <Stack
                  direction="row"
                  spacing={1}
                  alignItems="center"
                  justifyContent="center"
                >
                  <Typography
                    variant="subtitle1"
                    color="textSecondary"
                    style={{ fontWeight: 600 }}
                  >
                    Sua Nota:
                  </Typography>
                  <Rating
                    value={userRating}
                    precision={0.5}
                    onChange={(event, newValue) => handleRatingChange(newValue)}
                  />
                  <Typography
                    variant="subtitle1"
                    style={{ fontWeight: 700, color: "#1976d2" }}
                  >
                    ({userRating})
                  </Typography>
                </Stack>

                {/* CAMPO DE COMENTRIO */}
                <TextField
                  label="Adicionar Comentrio (Opcional)"
                  multiline
                  rows={2}
                  fullWidth
                  value={userComment}
                  onChange={(e) => setUserComment(e.target.value)}
                  onBlur={() => handleSaveComment()}
                  variant="outlined"
                />
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => handleSaveComment()}
                  disabled={userRating === 0}
                  style={{ marginTop: "8px" }}
                >
                  Salvar Classificao e Comentrio
                </Button>
              </Stack>
            </Card>

            {/* 4. INGREDIENTES NECESSRIOS */}
            <Card
              style={{
                padding: 16,
                borderRadius: 16,
                boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
              }}
            >
              <Typography
                variant="h5"
                style={{ fontWeight: 700, marginBottom: "16px" }}
              >
                Ingredientes
              </Typography>
              <Grid container spacing={2}>
                {recipe.extendedIngredients &&
                  recipe.extendedIngredients.map((ing, index) => (
                    <Grid item xs={6} sm={4} key={index}>
                      <Stack direction="row" spacing={1}>
                        <Typography
                          variant="body2"
                          style={{ fontWeight: 700, color: "#1976d2" }}
                        >
                          
                        </Typography>
                        <Typography variant="body2">
                          {ing.amount} {ing.unit} de **{ing.nameClean}**
                        </Typography>
                      </Stack>
                    </Grid>
                  ))}
              </Grid>
            </Card>

            {/* 5. MODO DE PREPARO */}
            <Card
              style={{
                padding: 16,
                borderRadius: 16,
                boxShadow: "0 4px 12px rgba(0,0,0,0.1)",
              }}
            >
              <Typography
                variant="h5"
                style={{ fontWeight: 700, marginBottom: "16px" }}
              >
                Modo de Preparo
              </Typography>
              {instructions ? (
                <Typography
                  variant="body1"
                  style={{ lineHeight: 1.6, whiteSpace: "pre-wrap" }}
                >
                  {instructions}
                </Typography>
              ) : (
                <Typography variant="body1" color="textSecondary">
                  Modo de preparo detalhado no disponvel.
                </Typography>
              )}
            </Card>

            {/* Boto de Retorno */}
            <Button
              variant="outlined"
              color="secondary"
              onClick={() => window.history.back()}
              style={{ marginTop: "16px" }}
            >
              Voltar para a Busca
            </Button>
          </Stack>
        </Grid>
      </Grid>

      {/* Snackbar para feedback */}
      <Snackbar
        open={showSnackbar}
        autoHideDuration={3000}
        onClose={() => setShowSnackbar(false)}
        message={snackbarMsg}
      />
    </>
  );
};

export default RecipeDetails;
