import { BrowserRouter, Routes, Route } from "react-router-dom";

// Importaes de Autenticao e Home
import Login from "./pages/authentication/Login";
import Register from "./pages/authentication/Register";
import AuthRedirect from "./pages/authentication/AuthRedirect";
import UpdatePassword from "./pages/authentication/UpdatePassword";
import Home from "./pages/Home";

// NOVO: Componente de Detalhes da Receita
import RecipeDetails from "./pages/RecipeDetails";

function App() {
  return (
    <BrowserRouter>
      <Routes>
        {/* Rotas de Autenticao */}
        <Route path="/" element={<AuthRedirect />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/update-password" element={<UpdatePassword />} />

        {/* Rotas Principais */}
        <Route path="/home" element={<Home />} />

        {/* ROTA DE DETALHES DINMICA: Atende o Requisito da 2 Pgina */}
        <Route path="/receitas/:recipeId" element={<RecipeDetails />} />
        {/* A Rota /receitas/ agora aceita um ID (Ex: /receitas/644781) */}

        {/* Adicione outras rotas (Profile) aqui se necessrio */}
      </Routes>
    </BrowserRouter>
  );
}

export default App;
