import { supabase } from "./SupabaseClient";

// Esta funo auxiliar busca dados de forma genrica
const list = async (table, filter, limit) => {
  let query = supabase.from(table).select("*");

  if (filter) {
    Object.keys(filter).forEach((key) => {
      query = query.eq(key, filter[key]);
    });
  }

  if (limit) {
    query = query.range(0, limit - 1);
  }

  return await query;
};

const Database = {
  // Funes de manipulao de dados CRUD (mantidas)
  create: async (table, data) => {
    return await supabase.from(table).insert([data]).select();
  },
  update: async (table, data, id) => {
    return await supabase.from(table).update(data).eq("id", id).select();
  },
  delete: async (table, id) => {
    return await supabase.from(table).delete().eq("id", id).select();
  },

  // Busca apenas um registro (mantida)
  find: async (table, id) => {
    const { data, error } = await list(table, { id: id }, 1);
    if (error) {
      return null;
    }
    return data ? data[0] : null;
  },

  // Implementao da busca de ingredientes traduzidos (mantida)
  getTranslatedIngredients: async () => {
    const { data, error } = await supabase
      .from("ingredientes")
      .select("id, nome_pt, nome_en");

    if (error) {
      console.error("Error fetching translated ingredients:", error);
      throw error;
    }
    return data;
  },

  // FUNES DE FAVORITOS (mantidas)
  isFavorited: async (userId, recipeId) => {
    const { data, error } = await supabase
      .from("user_favorites")
      .select("id")
      // CORREO 1: Para UUID na busca
      .eq("user_id", `"${userId}"`) // Supabase exige aspas na query de filtro (String)
      .eq("recipe_id", recipeId)
      .single();

    return data !== null;
  },

  toggleFavorite: async (
    userId,
    recipeId,
    recipeTitle,
    isCurrentlyFavorited
  ) => {
    const tableName = "user_favorites";

    if (isCurrentlyFavorited) {
      const { error } = await supabase
        .from(tableName)
        .delete()
        // CORREO 1: Para UUID na busca
        .eq("user_id", `"${userId}"`)
        .eq("recipe_id", recipeId);

      if (error) throw error;
      return false;
    } else {
      const dataToSave = {
        user_id: userId,
        recipe_id: recipeId,
        recipe_title: recipeTitle,
      };
      const { error } = await Database.create(tableName, dataToSave);

      if (error) throw error;
      return true;
    }
  },

  //  FUNO PARA SALVAR/ATUALIZAR CLASSIFICAO COM COMENTRIO
  saveRating: async (userId, recipeId, ratingValue, comment = null) => {
    const tableName = "recipe_ratings";

    // Converte o recipeId para NUMBER no incio
    const numericRecipeId = Number(recipeId);

    // Dados a serem atualizados/inseridos
    const updateData = { ratingValue: ratingValue, comentario: comment };

    // 1. Tenta ATUALIZAR a classificao se ela j existir
    const { data, error: updateError } = await supabase
      .from(tableName)
      .update(updateData)
      // CORREO FINAL: Foramos as aspas simples para o UUID na query
      .match({ user_id: userId, recipe_id: numericRecipeId })
      .select();

    if (updateError && updateError.code !== "PGRST116") {
      throw updateError;
    }

    // 2. Se nenhuma linha foi atualizada (no existia), CRIA o novo rating
    if (!data || data.length === 0) {
      const dataToSave = {
        user_id: userId,
        recipe_id: numericRecipeId, // Usar o ID NUMRICO na insero
        ratingValue: ratingValue,
        comentario: comment,
      };
      const { error: createError } = await Database.create(
        tableName,
        dataToSave
      );
      if (createError) throw createError;
    }

    return true;
  },

  // Funes no implementadas (mantidas)
  list: async (table) => {
    const { data, error } = await supabase.from(table).select("*");
    if (error) {
      console.error(`Error listing table ${table}:`, error);
      throw error;
    }
    return data;
  },
};

export default Database;
