// A base URL para todas as requisies da Spoonacular
const BASE_URL = "https://api.spoonacular.com";
// A chave de API deve ser lida do .env
const API_KEY = process.env.REACT_APP_SPOONACULAR_API_KEY;

const RecipeAPI = {
  /**
   * Busca receitas com base em uma lista de ingredientes.
   * @param {string[]} ingredients - Array de nomes de ingredientes (ex: ['tomate', 'queijo']).
   * @returns {Promise<Object[]>} - Lista de receitas.
   */
  searchByIngredients: async (ingredients) => {
    if (!API_KEY) {
      throw new Error(
        "Chave de API da Spoonacular no configurada. Verifique o .env."
      );
    } // Converte o array para string separada por vrgulas para a API

    const ingredientList = ingredients.join(","); // Endpoint para buscar receitas por ingredientes

    const url = `${BASE_URL}/recipes/findByIngredients?apiKey=${API_KEY}&ingredients=${ingredientList}&number=10`;

    try {
      const response = await fetch(url);

      if (!response.ok) {
        // Lana um erro se o status HTTP no for 200 (OK)
        const errorData = await response.json();
        throw new Error(
          errorData.message ||
            `Erro na busca de receitas: Status ${response.status}`
        );
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error("Erro ao buscar receitas na Spoonacular:", error);
      throw error;
    }
  },
  /**
   * IMPLEMENTAO: Busca ingredientes usando o autocomplete da API.
   * @param {string} query - O termo de busca (ex: 'app', 'tomate').
   * @returns {Promise<Object[]>} - Lista de ingredientes com { id, name, image }.
   */ searchIngredientsAutocomplete: async (query) => {
    if (!API_KEY) {
      throw new Error(
        "Chave de API da Spoonacular no configurada. Verifique o .env."
      );
    } // O nmero '20'  um bom limite para a primeira tela de seleo

    const url = `${BASE_URL}/food/ingredients/autocomplete?apiKey=${API_KEY}&query=${query}&number=20`;

    try {
      const response = await fetch(url);

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(
          errorData.message ||
            `Erro na busca de ingredientes: Status ${response.status}`
        );
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error("Erro ao buscar ingredientes na Spoonacular:", error);
      throw error;
    }
  },
  /**
   * Busca detalhes de uma receita especfica por ID.
   * Necessrio para a pgina de detalhes (Requisito TP3).
   * @param {number} recipeId - O ID da receita.
   * @returns {Promise<Object>} - Detalhes completos da receita.
   */ getRecipeDetails: async (recipeId) => {
    if (!API_KEY) {
      throw new Error(
        "Chave de API da Spoonacular no configurada. Verifique o .env."
      );
    }

    const url = `${BASE_URL}/recipes/${recipeId}/information?apiKey=${API_KEY}`;

    try {
      const response = await fetch(url);

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(
          errorData.message ||
            `Erro na busca de detalhes: Status ${response.status}`
        );
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error(
        "Erro ao buscar detalhes da receita na Spoonacular:",
        error
      );
      throw error;
    }
  },
};

export default RecipeAPI;
