// src/pages/authentication/AuthRedirect.jsx

import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../../services/SupabaseClient";

const AuthRedirect = () => {
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    // 1. Checagem de URL para Token de Recuperao (Execuo Imediata)
    // O Supabase limpa o hash (token) da URL rapidamente. Checamos ele aqui primeiro
    // para capturar o parmetro 'type=recovery' antes que ele seja removido.
    const urlParams = new URLSearchParams(window.location.hash.substring(1));
    const isRecoveryUrl = urlParams.get("type") === "recovery";

    if (isRecoveryUrl) {
      console.log(
        "URL de recuperao detectada. Redirecionando para atualizao de senha."
      );
      setLoading(false);

      // Redireciona imediatamente para o formulrio de nova senha
      navigate("/update-password", { replace: true });

      return; // Interrompe o restante do useEffect.
    }

    // 2. Listener de Autenticao (para login/logout normal, ignorado se for recovery)
    const { data: authListener } = supabase.auth.onAuthStateChange(
      (event, session) => {
        setLoading(false);

        // Lgica para Fluxo de Autenticao Normal (Login/Home)
        if (session) {
          // Se o usurio est autenticado e NO  recuperao (pois j saiu no if acima)
          navigate("/home", { replace: true });
        } else {
          // Caso contrrio, sem sesso, vai para o Login
          navigate("/login", { replace: true });
        }
      }
    );

    // Limpa o listener quando o componente for desmontado
    return () => {
      if (authListener && authListener.subscription) {
        authListener.subscription.unsubscribe();
      }
    };
  }, [navigate]);

  return (
    <div
      style={{
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        fontSize: "20px",
      }}
    >
      {loading
        ? "Verificando autenticao e redirecionando..."
        : "Redirecionando..."}
    </div>
  );
};

export default AuthRedirect;
